body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

*{
    border: none!important;
}
html{
    padding: 1em;
    background-image: linear-gradient(rgba(129, 69, 0, 0.8),rgba(253, 192, 92, 0.8));
    background-image: linear-gradient(rgba(231, 110, 255, 0.4),rgba(83, 147, 212, 0.4));
    background-repeat: no-repeat;
    min-height: 100%;
}
.ql-toolbar{
    max-width: 65em;
    border-radius: 0.2em;
    margin: auto;
    position: -webkit-sticky;
    position: sticky;
    border: none;
    box-shadow: 0  0.1em 1em rgba(00,00,00,0.4);
    background-color:azure;
    top: 0;
    z-index: 1;
}
.hidden{
  visibility: hidden;
}
.stky{
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.ql-container{
  margin: 2em;
  border: none;
  border-radius: 0.4em;
  box-shadow: 0 1em 2em 1em rgba(00,00,00,0.2);
  margin: 7em auto 4em;
  max-width: 80em;
  background-color:azure;
}
.ql-editor{
  height: 90%;
  padding: 4em;
}
::-webkit-scrollbar {
  width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  width: 10px;
  border-radius: 10px;
  background:rgba(00,00,00,0.2); 
  
}
   
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
.title{
  background-color:azure;
  padding: 8px;
  font-size:32px;
  border-radius: 5px;
  -webkit-transform: translateY(3.5em);
          transform: translateY(3.5em);
  opacity: 60%;
  }
  .title:focus{
    box-shadow: 0  0.1em 1em rgba(00,00,00,0.4);
    opacity: 100%;
    outline: none;
  }
  
.fileSystem{
    width: 80vw;
    margin: 8vw auto 5em;
    background-color: transparent;
    display: flex;
    flex-direction: row;
    flex-wrap:wrap;
    justify-content: start;
    align-items: center;
}
.fileContainer{
    display: flex;
    flex-direction: column;
    margin: 1em 0.8em;
    max-width: 5em;
    background-color: #f3f3f3;
    padding: 1em 2em;
    border-radius: 10px;
    box-shadow: 0.5em 0.5em 0.6em 0.5em rgba(1,1,1,0.2);
    height: 10em;
}

.fileimg{
    margin: 1em 1em 0;
    width: 3em;
    min-height: 4em;
    overflow: hidden;
}
.contextMenu{
    box-shadow: 0.5em 0.5em 0.6em 0.5em rgba(1,1,1,0.2);
    z-index: 2;
    background-color: #f2f2f2;
    border-radius: 0.2em;
    min-width: 8em;
    position: absolute;
    -webkit-animation: renderMenu;
            animation: renderMenu;
    transition: all ease-in-out 0.3s;
}
.contextMenuItem{
    z-index: 3;
    padding: 0.5em;
    cursor: pointer;
}
.contextMenuItemRed:hover{
    border-radius: 0.2em;
    color:white;
    background-color: rgba(255, 67, 67, 0.9);
}

@-webkit-keyframes renderMenu {
    from{
        height: 0!important;
    }
    to{
        height: 4em;
    }
}

@keyframes renderMenu {
    from{
        height: 0!important;
    }
    to{
        height: 4em;
    }
}
a{
    text-decoration: none;
    color: #000;
}
